/*eslint-disable */
import React from "react";
import Footer from "./component/Footer";
import Header from "./component/Header";
import { useLocation } from "react-router-dom";
import { useHistory } from "react-router-dom";

function Download(props) {
  let history = useHistory();
  const location = useLocation();
  console.log(location.state);
  const DownloadLink = () => {
    if (location.state == undefined) {
      history.push("/");
      return;
    } else {
      const details = location.state.detail;
      return (
        <>
          <div class="abuttons">
            <a
              href={details.nowm}
              onclick="showAd();sendEvent('click_download_mp4_snaptik')"
              class="abutton is-success is-fullwidth"
              rel="nofollow"
              title="Download Server 01"
            >
              <span class="span-icon">
                <i class="icon icon-download"></i>
                <span> Download Video Without WaterMark</span>
              </span>
            </a>
            <a
              href={details.wm}
              onclick="showAd();sendEvent('click_download_mp4_snapsave')"
              class="abutton is-success is-fullwidth"
              rel="nofollow"
              title="Download Server 02"
            >
              <span class="span-icon">
                <i class="icon icon-download"></i>
                <span> Download Video With WaterMark</span>
              </span>
            </a>
          </div>
        </>
      );
    }
  };

  return (
    <>
      <Header />

      <section class="section download">
        <div class="container">
          <iframe
            id="snaptik-frame"
            name="snaptik-frame"
            style={{ display: "none" }}
          ></iframe>
          <div id="snaptik-video">
            <article class="snaptikvid">
              <div class="snaptik-left">
                <img
                  src="data:image/png;base64,"
                  class="lazy"
                  alt="Test"
                />
              </div>
              <div class="snaptik-middle center">
                <h3>Profile Images (Coming soon)</h3>
                <p>
                  <span>#Hastage #Coming Soon</span>
                </p>
                <p class="video-time">
                  <span class="blur">...</span>
                </p>
              </div>
            </article>

            <div id="download-block" class="download-block">
              {DownloadLink()}
            </div>
            <div class="row back-home">
              <a
                onclick="sendEvent('click_back_home')"
                href="/"
                class="abutton btn-secondary is-fullwidth"
                title="Download other video"
              >
                <span class="span-icon">
                  <i class="icon icon-back"></i>
                  <span>Download other video</span>
                </span>
              </a>
            </div>
          </div>
        </div>
      </section>

      <Footer />
    </>
  );
}

export default Download;
